import { Injectable } from '@angular/core';

/**
 * 画面ごとの権限区分
 */
enum AuthType{
  /**使用できない */
  NotUse= 0,
  /**閲覧のみ可能 */
  View= 1,
  /**更新も可能 */
  Updatable = 2
}
/**
 * 画面ごとの権限管理
 */
@Injectable({
  providedIn: 'root'
})
export class AuthKbnService {

  authKbnList: any = {};

  constructor() { }

  clear(){
    this.authKbnList = {};
  }

}
